import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex'
import store from '../store'
import i18n from "../i18n"

import Auth from '@/views/Auth/Auth'
import Signin from '@/views/Auth/Signin'
import SigninIdentifier from '@/views/Auth/SigninIdentifier'
import SigninPassword from '@/views/Auth/SigninPassword'
import Signup from '@/views/Auth/Signup'
import ResetPassword from '@/views/Auth/ResetPassword'
import Dashboard from '../views/Dashboard/Dashboard'

Vue.use(VueRouter)

const routes = [
  {
    // redirigir
    path: '/',
    redirect: '/auth/signin/identifier'
  },
  
  {
    path: '/auth',
    component: Auth,
    children: [
      {
        path: 'signin',
        component: Signin,
        meta: {
          libre: true
        },
        children: [
          {
            path: 'identifier',
            name: 'signin',
            component: SigninIdentifier,
            meta: {
              libre: true
            },
          }, {
            path: 'password',
            name: 'password',
            component: SigninPassword,
            meta: {
              libre: true
            },
          },{
            path: 'reset',
            component: ResetPassword,
            name: 'reset',
            meta: {
              libre: true
            },
          }
        ]
      }, {
        path: 'signup',
        component: Signup,
        name: 'signup',
        meta: {
          libre: true
        },
      }
    ]
  },
  {
    path: '/',
    name: 'Dashboard',
    component: Dashboard,
    meta: {
      administrator: true,
      employee: true
    }
  }
  

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.libre)) {
    next();
  } else if (store.state.user && store.state.user.rol == 'administrator') {
    if (to.matched.some(record => record.meta.administrator)) {
      next();
    }
  } else if (store.state.user && store.state.user.rol == 'employee') {
    if (to.matched.some(record => record.meta.employee)) {
      next();
    }
  }
  if (to.query.res === 'unknow') {
    router.app.$store.dispatch('setSnackbar', { color: 'red darken-2', text: router.app.$t('sign-up.error-unknow'), timeout: '5000' })
    next({ name: 'signin' });
  } else if (to.query.res === 'invalid') {
    router.app.$store.dispatch('setSnackbar', { color: 'red darken-2', text: router.app.$t('sign-up.error-invalid'), timeout: '5000' })
    next({ name: 'signin' });
  } else if (to.query.res === 'confirmed') {
    router.app.$store.dispatch('setSnackbar', { color: 'red darken-2', text: router.app.$t('sign-up.error-confirmed'), timeout: '5000' })
    next({ name: 'signin' });
  } else if (to.query.res === 'activate') {
    router.app.$store.dispatch('setSnackbar', { color: 'green darken-2', text: router.app.$t('sign-up.error-activate'), timeout: '5000' })
    next({ name: 'signin' });
  } else if (to.query.res === 'expired') {
    router.app.$store.dispatch('setSnackbar', { color: 'red darken-2', text: router.app.$t('sign-up.error-expired'), timeout: '5000' })
    next({ name: 'signin' });
  } else if (to.query.res === 'invalidp') {
    router.app.$store.dispatch('setSnackbar', { color: 'red darken-2', text: router.app.$t('reset-password.error-invalidp'), timeout: '5000' })
    next({ name: 'signin' });
  }

  //router.redirect('/');

})

export default router
