<template>
  <div>
    <div class="text-center">
      <h6 class="headline">
        {{ $t("sign-in.title") }}
      </h6>
      <span class="d-inline-block mb-8">{{ $t("sign-in.subtitle") }}</span>
    </div>

    <v-form @submit.prevent="next" ref="form_auth">
      <v-text-field
        ref="input"
        v-model="identifier"
        class="mb-8"
        :label="$t('sign-in.label_email')"
        name="login"
        type="text"
        hide-details="auto"
        outlined
        :disabled="disabled"
        :error-messages="error"
        autofocus
      />
    </v-form>

    <div class="text-body-2 text--secondary mb-8">
      {{ $t("sign-in.private") }}
      <a
        href="#"
        class="d-inline-block text-none text-decoration-none font-weight-bold"
        >{{ $t("sign-in.learn-more") }}</a
      >
    </div>
    <div class="d-flex justify-space-between">
      <v-btn
        class="text-none letter-spacing-0"
        color="primary"
        text
        @click="$router.push({ name: 'signup' })"
      >
        {{ $t("sign-in.create-account") }}
      </v-btn>
      <v-btn
        class="text-none"
        style="min-width: 88px"
        color="primary"
        depressed
        @click="next"
        @keyup.enter="next()"
      >
        {{ $t("sign-in.next") }}
      </v-btn>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { wip } from "@/helpers.js";

export default {
  data: () => ({
    error: null,
    disabled: false,
  }),

  computed: {
    identifier: {
      get() {
        return this.$store.state.identifier;
      },
      set(value) {
        this.$store.commit("updateIdentifier", value);
      },
    },
  },

  created() {
    this.email = "";
    this.password = "";
    this.router_validate();
  },

  methods: {
    router_validate(){
        
        if ( this.$route.query.res === 'resetpassword') {
          this.$router.push({ name: "reset", query: { key: this.$route.query.key, user:this.$route.query.user }});
        }
         
    },

    next() {
      if (!this.validEmail(this.identifier)) {
        this.error = this.$t("sign-in.email-error");
        this.$refs.input.focus();
        return;
      }

      axios
        .post("/user/check_email", { email: this.identifier })
        .then((response) => {
          this.error = null;
          this.$emit("next", this.identifier);
        })
        .catch((error) => {
          if (error.response.data == "unknown") {
            this.error = this.$t("sign-in.email-fail");
            this.$refs.input.focus();
            return;
          } else if (error.response.data == "fail_login") {
            this.error = this.$t("sign-in-password.inactive");
            this.$refs.input.focus();
            return;
          } else if (error.response.data == "inactive") {
            this.error = this.$t("sign-in.user-status");
            this.$refs.input.focus();
            return;
          } else if (error.response.data == "unverified") {
            this.error = this.$t("sign-in.user-verified");
            this.$refs.input.focus();
            return;
          }
        });
    },
    validEmail(email) {
      var re =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },

    wip,
  },
  watch: {},
};
</script>
