<template>
  <div class="d-flex justify-space-between mt-6">
    <v-row align="center">
      <v-col
          class="d-flex"
          cols="6"
          sm="6"
      >
    <v-select
        v-model="$i18n.locale"
        :items="locales"
        menu-props="auto"
        hide-details
        prepend-icon="language"
        append-icon="expand_more"
        dense
        style="font-size: 13px"
        color="secondary"
    ></v-select>
    </v-col>
    <div class="text-center">
      <v-btn
        class="text-none letter-spacing-0"
        color="secondary"
        text
        small
      >
        {{ $t("footer.help") }}
      </v-btn>
      <v-btn
        class="text-none letter-spacing-0"
        color="secondary"
        text
        small
      >
        {{ $t("footer.privacy") }}
      </v-btn>
      <v-btn
        class="text-none letter-spacing-0"
        color="secondary"
        text
        small
      >
        {{ $t("footer.terms") }}
      </v-btn>
    </div>
    </v-row>
  </div>

</template>

<script>
export default {
  data: () => ({
    locales: [
      { value: "es", text: "Español (España)" },
      { value: "en", text: "English (United States)" }
    ]
  }),
  watch: {
    "$i18n.locale": {
      handler(locale) {
        localStorage.setItem("locale", locale);
      }
    }
  }
}
</script>

