<template>
  <div>
    <v-container class="pa-0">
      <v-row align="center" >
        <v-col cols="12">
          <div align="center">
              <img
                src="/horizontal.svg"
                alt=""
                width="50%"
              />
            <h1 class="text-h6 mb-8">
            {{ $t("sign-up.create") }}
          </h1>
          </div>
          
          <v-form @submit.prevent="register">
            <v-container class="pa-0">
              <v-row>
                <v-col cols="12" md="5">
                  <v-text-field
                    ref="input"
                    v-model="name"
                    :label="$t('sign-up.firstname')"
                    name="login"
                    type="text"
                    hide-details="auto"
                    outlined
                    :error-messages="nameErrors"
                    @input="$v.name.$touch()"
                    @blur="$v.name.$touch()"
                    required
                    dense
                    autofocus
                    @keypress="isLetter($event)"
                    @keyup="normalize($event, 'name')"
                    class="my-uppercase"
                  />
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    ref="input"
                    v-model="middle_name"
                    :label="$t('sign-up.middle-initial')"
                    name="login"
                    type="text"
                    hide-details="auto"
                    outlined
                    :error-messages="error"
                    dense
                    :maxlength="1"
                    @keypress="isLetter($event)"
                    @keyup="normalize($event, 'middle')"
                    class="my-uppercase"
                  />
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    ref="input"
                    v-model="lastname"
                    :label="$t('sign-up.lastname')"
                    name="login"
                    type="text"
                    hide-details="auto"
                    outlined
                    :error-messages="lastnameErrors"
                    @input="$v.lastname.$touch()"
                    @blur="$v.lastname.$touch()"
                    dense
                    @keypress="isLetter($event)"
                    @keyup="normalize($event, 'lastname')"
                    class="my-uppercase"
                  />
                </v-col>
              </v-row>
              <v-row>
              </v-row>
              <v-row>
                <v-col>
                  <v-text-field
                    ref="input"
                    v-model="email"
                    :label="$t('sign-up.email')"
                    name="login"
                    type="text"
                    hide-details="auto"
                    persistent-hint
                    outlined
                    :error-messages="emailErrors"
                    @input="$v.email.$touch()"
                    @blur="$v.email.$touch()"
                    @paste.prevent
                    dense
                  />
                </v-col>
                <v-col>
                  <v-text-field
                    ref="input"
                    v-model="emailconfirm"
                    :label="$t('sign-up.email_confirm')"
                    name="login"
                    type="text"
                    hide-details="auto"
                    persistent-hint
                    outlined
                    :error-messages="emailConfirmErrors"
                    @input="$v.emailconfirm.$touch()"
                    @blur="$v.emailconfirm.$touch()"
                    @paste.prevent
                    dense
                  />
                </v-col>
              </v-row>

              <v-row>
                <v-col>
                  <v-text-field
                    ref="input"
                    v-model="password"
                    :label="$t('sign-up.password')"
                    name="login"
                    :append-icon="show1 ? 'visibility' : 'visibility_off'"
                    :type="show1 ? 'text' : 'password'"
                    hide-details="auto"
                    outlined
                    :error-messages="passwordErrors"
                    @input="$v.password.$touch()"
                    @blur="$v.password.$touch()"
                    dense
                    @click:append="show1 = !show1"
                  />
                </v-col>

                <v-col>
                  <v-text-field
                    ref="input"
                    :append-icon="show2 ? 'visibility' : 'visibility_off'"
                    v-model="confirmPassword"
                    :label="$t('sign-up.password_confirm')"
                    :type="show2 ? 'text' : 'password'"
                    hide-details="auto"
                    outlined
                    :error-messages="passwordConfirm"
                    @input="$v.confirmPassword.$touch()"
                    @blur="$v.confirmPassword.$touch()"
                    dense
                    @click:append="show2 = !show2"
                  />
                </v-col>
              </v-row>

              <div class="body-2 mt-2" v-if="$v.password.$anyError">
                <v-chip
                  class="mr-2 mt-2"
                  small
                  outlined
                  :color="
                    $v.password.containsUppercase
                      ? 'green'
                      : $v.password.$anyError
                      ? 'red'
                      : 'red'
                  "
                >
                  <v-avatar left v-if="$v.password.containsUppercase"
                    ><v-icon small>done</v-icon> </v-avatar
                  >{{ $t("sign-up.errors-password-uppercase") }}
                </v-chip>
                <v-chip
                  class="mr-2 mt-2"
                  small
                  outlined
                  :color="
                    $v.password.containsLowercase
                      ? 'green'
                      : $v.password.$anyError
                      ? 'red'
                      : 'red'
                  "
                >
                  <v-avatar left v-if="$v.password.containsLowercase"
                    ><v-icon small>done</v-icon> </v-avatar
                  >{{ $t("sign-up.errors-password-lowercase") }}</v-chip
                >
                <v-chip
                  class="mr-2 mt-2"
                  small
                  outlined
                  :color="
                    $v.password.containsNumber
                      ? 'green'
                      : $v.password.$anyError
                      ? 'red'
                      : 'red'
                  "
                >
                  <v-avatar left v-if="$v.password.containsNumber"
                    ><v-icon small>done</v-icon> </v-avatar
                  >{{ $t("sign-up.errors-password-number") }}</v-chip
                >
                <v-chip
                  class="mt-2 mr-2"
                  small
                  outlined
                  :color="
                    $v.password.containsSpecial
                      ? 'green'
                      : $v.password.$anyError
                      ? 'red'
                      : 'red'
                  "
                >
                  <v-avatar left v-if="$v.password.containsSpecial"
                    ><v-icon small>done</v-icon> </v-avatar
                  >{{ $t("sign-up.errors-password-contains_special") }}</v-chip
                >
                <v-chip
                  class="mt-2"
                  small
                  outlined
                  :color="
                    password.length >= 8
                      ? 'green'
                      : $v.password.$anyError
                      ? 'red'
                      : 'red'
                  "
                >
                  <v-avatar left v-if="password.length >= 8"
                    ><v-icon small>done</v-icon> </v-avatar
                  >{{ $t("sign-up.errors-password-characters") }}</v-chip
                >
              </div>
            </v-container>
            <div class="transition-wrapper">
              <div class="d-flex justify-space-between mt-8">
                <v-btn
                  :disabled="loading"
                  class="text-none letter-spacing-0"
                  color="primary"
                  text
                  @click="$router.push({ name: 'signin' })"
                >
                  {{ $t("sign-up.instead") }}
                </v-btn>
                <v-btn
                  ref="btn_register"
                  class="text-none letter-spacing-0"
                  style="min-width: 88px"
                  color="primary"
                  depressed
                  :loading="loading"
                  :disabled="loading"
                  @click="register"
                  @keyup.enter="register()"
                >
                  {{ $t("sign-up.next") }}
                </v-btn>
              </div>
              <!--<transition :name="transitionName">
                <router-view @next="$emit('next', $event)" />
              </transition>-->
            </div>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>


<script>
import axios from "axios";
import { required, minLength, sameAs, email } from "vuelidate/lib/validators";

export default {
  data: () => ({
    show1: false,
    show2: false,
    loading: false,
    transitionName: "",
    error: "",
    name: "",
    lastname: "",
    middle_name: "",
    email: "",
    emailconfirm: "",
    isUnique: false,
    password: "",
    confirmPassword: "",
  
  }),

  watch: {
    loader() {
      const l = this.loader;
      this[l] = !this[l];

      setTimeout(() => (this[l] = false), 3000);

      this.loader = null;
    },
  },
  computed: {
    nameErrors() {
      const errors = [];
      const message_name_required = this.$t("sign-up.errors-name");

      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.required && errors.push(message_name_required);
      return errors;
    },

    lastnameErrors() {
      const errors = [];
      const message_lastname_required = this.$t("sign-up.errors-lastname");

      if (!this.$v.lastname.$dirty) return errors;
      !this.$v.lastname.required && errors.push(message_lastname_required);
      return errors;
    },

    emailErrors() {
      const errors = [];
      const message_email_required = this.$t("sign-up.errors-email-required");
      const message_email_isunique = this.$t("sign-up.errors-email-isunique");
      const message_email_valid = this.$t("sign-up.errors-email-valid");

      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.required && errors.push(message_email_required);
      !this.$v.email.email && errors.push(message_email_valid);
      !this.$v.email.isUnique && errors.push(message_email_isunique);
      return errors;
    },
    emailConfirmErrors() {
      const errors = [];
      const message_email_confirm_required = this.$t(
        "sign-up.errors-email-confirm-required"
      );
      const message_email_confirm_coincidence = this.$t(
        "sign-up.errors-email-confirm-coincidence"
      );

      if (!this.$v.emailconfirm.$dirty) return errors;
      !this.$v.emailconfirm.required &&
        errors.push(message_email_confirm_required);
      !this.$v.emailconfirm.sameAs &&
        errors.push(message_email_confirm_coincidence);
      return errors;
    },

    passwordErrors() {
      const errors = [];
      const message_password_required = this.$t(
        "sign-up.errors-password-required"
      );
      const message_password_weak = this.$t("sign-up.errors-password-weak");

      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required && errors.push(message_password_required);
      !this.$v.password.minLength && errors.push(message_password_weak);
      !this.$v.password.containsUppercase && errors.push(message_password_weak);
      !this.$v.password.containsLowercase && errors.push(message_password_weak);
      !this.$v.password.containsNumber && errors.push(message_password_weak);
      !this.$v.password.containsSpecial && errors.push(message_password_weak);
      return errors;
    },
    passwordConfirm() {
      const errors = [];
      const message_password_confirm_required = this.$t(
        "sign-up.errors-password-confirm-required"
      );
      const message_password_confirm_coincidence = this.$t(
        "sign-up.errors-password-confirm-coincidence"
      );

      if (!this.$v.confirmPassword.$dirty) return errors;
      !this.$v.confirmPassword.required &&
        errors.push(message_password_confirm_required);
      !this.$v.confirmPassword.sameAs &&
        errors.push(message_password_confirm_coincidence);
      return errors;
    },
  },
  validations: {
    name: { required },
    lastname: { required },
    email: {
      required,
      email,
      isUnique(value) {
        // standalone validator ideally should not assume a field is required
        if (value === "") return true;
        return axios
          .post("/user/verify_email", { email: this.email })
          .then((response) => {
            return false;
          })
          .catch((error) => {
            return true;
          });
      },
    },
    emailconfirm: { required, sameAs: sameAs("email") },
    password: {
      required,
      minLength: minLength(8),
      containsUppercase: function (value) {
        return /[A-Z]/.test(value);
      },
      containsLowercase: function (value) {
        return /[a-z]/.test(value);
      },
      containsNumber: function (value) {
        return /[0-9]/.test(value);
      },
      containsSpecial: function (value) {
        return /[#?!@$%^&*-]/.test(value);
      },
    },
    confirmPassword: { required, sameAs: sameAs("password") },
  },
  methods: {
    isLetter(e) {
      let char = String.fromCharCode(e.keyCode);
      if (/^[A-Za-z ñÑ]+$/.test(char)) return true;
      else e.preventDefault();
    },

    normalize(e, field) {
      if (field === "name") {
        this.name = e.target.value
          .normalize("NFD")
          .replace(/[\u0300-\u036f]/g, "");
      } else if (field === "middle") {
        this.middle_name = e.target.value
          .normalize("NFD")
          .replace(/[\u0300-\u036f]/g, "");
      } else if (field === "lastname") {
        this.lastname = e.target.value
          .normalize("NFD")
          .replace(/[\u0300-\u036f]/g, "");
      }
    },

    register() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      } else {
        this.loading = true;

        const error_register_user = this.$t("sign-up.register-error");
        const info_success_register = this.$t("sign-up.register-success");

        // Registro de Usuario
        axios
          .post("/user/register", {
            name: this.name.toUpperCase(),
            last_name: this.lastname.toUpperCase(),
            middle_name: this.middle_name.toUpperCase(),
            password: this.password,
            email: this.email,
          })
          .then((response) => {
            
            this.$store.dispatch('setSnackbar', { color: 'green darken-2', text: info_success_register, timeout: '5000' })
            this.$router.push({ name: "signin" });
    
          })
          .catch((error) => {
            this.loading = false;
            this.$store.dispatch("setSnackbar", {
              color: "red darken-2",
              text: error_register_user,
              timeout: "5000",
            });
            this.$router.push({ name: "signup" });
          });
      }
    },
  },
  watch: {
    dto: function (val) {
      this.dto = val.toUpperCase();
    },
  },
};
</script>

<style>
.my-uppercase input {
  text-transform: uppercase;
}
</style>
