<template>
  <div>
    <div class="text-center w-100">
      <h1 class="text-h5 mb-2" v-if="!success_restore">
        {{ $t("sign-in-password.title") }}
      </h1>
      <v-chip
        class="mb-10"
        outlined
        link
        :disabled="loading"
        @click="$router.push({ name: 'signin' })"
        v-if="!success_restore"
      >
        <v-avatar left>
          <v-icon color="secondary"> account_circle </v-icon>
        </v-avatar>
        {{ email }}
        <v-avatar right>
          <v-icon color="secondary"> expand_more </v-icon>
        </v-avatar>
      </v-chip>

      <v-form @submit.prevent="login" v-if="!success_restore">
        <v-text-field
          ref="input_password"
          class="mb-10"
          :append-icon="show ? 'mdi-eye-off-outline' : 'mdi-eye-outline'"
          :label="$t('sign-in-password.enter-password')"
          :type="show ? 'input' : 'password'"
          hide-details="auto"
          outlined
          :disabled="loading"
          v-model="password"
          :error-messages="error"
          autofocus
        />
      </v-form>

      <div class="d-flex justify-space-between" v-if="!success_restore">
        <v-btn
          class="text-none letter-spacing-0 font-weight-bold"
          color="primary"
          text
          :loading="loading"
          :disabled="loading"
          @click="restore()"
        >
          {{ $t("sign-in-password.forgot-password") }}
        </v-btn>
        <v-btn
          class="text-none"
          style="min-width: 88px"
          color="primary"
          depressed
          :disabled="loading"
          @click="login"
          @keyup.enter="login()"
        >
          {{ $t("sign-in-password.next") }}
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { wip } from "@/helpers.js";

export default {
  data: () => ({
    success_restore: false,
    error: "",
    disabled: false,
    show: false,
    email: "",
    loading: false,
  }),

  computed: {
    password: {
      get() {
        return this.$store.state.password;
      },
      set(value) {
        this.$store.commit("updatePassword", value);
      },
    },
  },

  created() {
    //Ejecutar Método para Obtener el email del usuario
    this.get_user_email();

    //Obtenemos el evento al recargar y ejecutamos el método para ir al Inicio
    window.addEventListener("beforeunload", this.recharge_page());

    this.password = "";
  },

  methods: {
    wip,

    //Método Obtener el email que inserta el usuario
    get_user_email() {
      this.email = this.$store.state.identifier;
    },

    //Método para ir al Inicio al recargar la página
    recharge_page() {
      if (this.email === "") {
        this.$router.push({ name: "signin" });
      }
    },

    login() {
      if (this.password == "") {
        this.error = this.$t("sign-in-password.password-error");
        this.$refs.input_password.focus();

        axios
          .post("user/fail_password", { email: this.email })
          .then((response) => {
            this.password = "";

            let error_inactive = this.$t("sign-in-password.inactive");
            let error_blocked = this.$t("sign-in-password.blocked");

            if (response.data == "attempt_two") {
              this.password = "";
              this.error = this.$t("sign-in-password.attempt_two");
            } else if (response.data == "blocked") {
              this.password = "";
              this.$store.dispatch("setSnackbar", {
                color: "red darken-2",
                text: error_blocked,
                timeout: "5000",
              });
            } else {
              this.$store.dispatch("setSnackbar", {
                color: "red darken-2",
                text: error_inactive,
                timeout: "5000",
              });
              this.password = "";
            }
          })
          .catch((error) => {
            console.log(error);
          });
        return;
      } else {
        axios
          .post("user/login", { email: this.email, password: this.password })
          .then((response) => {
            return response.data;
          })
          .then((data) => {
            this.$store.dispatch("guardarToken", data.tokenReturn);
            this.$router.push({ name: "Dashboard" });

            let initial_welcome_message = this.$t("dashboard.initial-welcome-message");
            let end_welcome_message = this.$t("dashboard.end-welcome-message");

            let name_user = data.user.name.toLowerCase();
            let lastname_user = data.user.last_name.toLowerCase();
            let name_view = name_user.charAt(0).toUpperCase() + name_user.substring(1, name_user.length);
            let lastname_view = lastname_user.charAt(0).toUpperCase() + lastname_user.substring(1, lastname_user.length);

            setTimeout(
              () =>
                this.$store.dispatch("setSnackbar", {
                  color: "green darken-2",
                  text:
                    initial_welcome_message +
                    name_view +
                    " " +
                    lastname_view +
                    end_welcome_message,
                  timeout: "3500",
                }),
              2500
            );
          })
          .catch((error) => {
            this.error = this.$t("sign-in-password.password-fail");
            this.password = "";

            axios
              .post("user/fail_password", { email: this.email })
              .then((response) => {
                let error_inactive = this.$t("sign-in-password.inactive");
                let error_blocked = this.$t("sign-in-password.blocked");

                if (response.data == "attempt_two") {
                  this.password = "";
                  this.error = this.$t("sign-in-password.attempt_two");
                } else if (response.data == "blocked") {
                  this.password = "";
                  this.$store.dispatch("setSnackbar", {
                    color: "red darken-2",
                    text: error_blocked,
                    timeout: "5000",
                  });
                } else {
                  this.password = "";
                  this.$store.dispatch("setSnackbar", {
                    color: "red darken-2",
                    text: error_inactive,
                    timeout: "5000",
                  });
                }
              })
              .catch((error) => {
                console.log(error);
              });
          });
      }
    },

    restore() {
      this.loading = true;
      axios
        .post("user/reset_password", { email: this.email })
        .then((response) => {
          let info_change_password = this.$t(
            "reset-password.info-change-password"
          );

          this.$store.dispatch("setSnackbar", {
            color: "orange darken-2",
            text: info_change_password,
            timeout: "5000",
          });
          this.$router.push({ name: "signin" });
        })
        .catch((error) => {
          this.loading = false;

          let error_change_password = this.$t(
            "reset-password.error-change-password"
          );

          this.$store.dispatch("setSnackbar", {
            color: "red darken-2",
            text: error_change_password,
            timeout: "5000",
          });
        });
    },
  },
  watch: {},
};
</script>
